<template>
  <va-card :title="$t('tables.labelAttendance')" class="w-100">
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        <va-icon name="fa fa-circle" :color="props.rowData.color" size="8px" />
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>
      <template slot="iS_Vacation" slot-scope="props">
        {{props.rowData.iS_Vacation  ? '&#10004;': '&#10008;' }}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
      </template>
    </va-data-table>

  </va-card>

</template>

<script>
import AttendanceService from '../../../../services/BasicData/AttendanceService'

export default {
  data () {
    return {
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {},
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
        sortField: 'date',

      }, {
        name: 'total_Attendance',
        title: this.$t('tables.headings.total_Attendance'),
        sortField: 'total_Attendance',

      }, {
        name: 'total_Absence',
        title: this.$t('tables.headings.total_Absence'),
        sortField: 'total_Absence',

      }, {
        name: '__slot:iS_Vacation',
        title: this.$t('tables.headings.iS_Vacation'),

      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    readItems (page = 1) {
      this.loading = true

      const params = {
        PageSize: this.perPage,
        PageNumber: page,
      }
      AttendanceService.getAllPagnation(params)
        .then(response => {
          console.log('dddd')
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },
  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
